.workspace-3d-container {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.workspace-info {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  background: linear-gradient(135deg, #6366f1, #ec4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  z-index: 10;
  pointer-events: none;
}

.workspace-controls {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  color: #a0a0b0;
  text-align: center;
  font-size: 14px;
  background: rgba(26, 26, 46, 0.8);
  backdrop-filter: blur(10px);
  padding: 15px 30px;
  border-radius: 10px;
  border: 1px solid rgba(99, 102, 241, 0.3);
  z-index: 10;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .workspace-info {
    font-size: 16px;
    top: 15px;
    padding: 0 20px;
  }
  
  .workspace-controls {
    font-size: 12px;
    padding: 12px 20px;
    bottom: 15px;
    max-width: 90%;
  }
}

@media (max-width: 480px) {
  .workspace-info {
    font-size: 14px;
    top: 10px;
    padding: 0 15px;
  }
  
  .workspace-controls {
    font-size: 11px;
    padding: 10px 15px;
    bottom: 10px;
  }
}
